#ptc-completionist_pinned-tasks .inside {
  padding: 0;
  margin: 0;
}

#ptc-completionist_pinned-tasks .inside button {
  display: inline-block;
  width: 1.75em;
  height: 1.75em;
  font-size: 0.9em;
  padding: 0;
  border-radius: 999px;
  outline: none;
  box-shadow: none;
  border: 1px solid #486581;
}

#ptc-completionist_pinned-tasks .inside button[disabled],
#ptc-completionist_pinned-tasks .inside input[disabled] {
  background: #F5F7FA !important;
  border-color: #9FB3C8 !important;
  color: #9FB3C8 !important;
  text-shadow: none !important;
}

#ptc-completionist_pinned-tasks .inside button[disabled] {
  background: #D9E2EC !important;
}

#ptc-completionist_pinned-tasks .inside input::placeholder {
  color: #9AA5B1;
}

/* Reload Task List Button */
#ptc-completionist_pinned-tasks button#reload-task-list {
  display: block;
  float: right;
  margin: 6px 0px;
  width: auto;
  height: calc(36px - (6px * 2));

  background: #627D98;
  border-radius: 3px;
  border: 1px solid #486581;
  text-shadow: 1px 2px 2px #486581;
  text-decoration: none;
  color: #FFF;
  font-size: 0.9em;
  padding: 0 1.2em;
  font-weight: 400;
}

#ptc-completionist_pinned-tasks button#reload-task-list i {
  margin-right: 0.4em;
}

#ptc-completionist_pinned-tasks button#reload-task-list[disabled] {
  background: #F5F7FA;
  border-color: #9FB3C8;
  color: #9FB3C8;
  text-shadow: none;
}

/* Pin a Task Toolbar */
#ptc-completionist_pinned-tasks aside#pin-a-task {
  background: #F5F7FA;
  border-bottom: 1px solid #CBD2D9;
  padding: 7px 10px;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #task-toolbar {
  display: flex;
  align-items: center;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #task-toolbar button {
  width: 2em;
  height: 2em;
  font-size: 1em;
  margin-left: 5px;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #task-toolbar input#asana-task-link-url {
  flex: 1;
  display: inline-block;
  height: 2em;
  min-height: 0;
  padding: 0 2.3em 0 1em;
  border-radius: 999px;
  border: 1px solid #1992D4;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #task-toolbar button#submit-pin-existing {
  margin-left: calc(-2em - 2px);
  background: #40C3F7;
  border-color: #2BB0ED;
  color: #035388;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #task-toolbar button#toggle-create-new {
  background: #6CD410;
  border-color: #399709;
  color: #1E5303;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #task-toolbar button#toggle-create-new.open {
  background: #E12D39;
  border-color: #8A041A;
  color: #610316;
}

/* New Task Form */
#ptc-completionist_pinned-tasks aside#pin-a-task #pin-new-task {
  margin: 1em 0 0;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #pin-new-task .form-group {
  display: flex;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #pin-new-task .form-group label {
  flex: 1;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #pin-new-task .form-group input {
  flex: 2;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #pin-new-task button#submit-create-new {
  width: auto;
  height: auto;
  font-size: 1em;
  background: #6CD410;
  border-radius: 999px;
  border: 1px solid #399709;
  text-decoration: none;
  color: #1E5303;
  font-size: 0.9em;
  padding: 0.3em 0.75em 0.35em;
  font-weight: 600;
  margin: 0.75em 0;
}

#ptc-completionist_pinned-tasks aside#pin-a-task #pin-new-task button#submit-create-new i {
  margin-right: 0.4em;
}
